#header {
    transform: translateY(-200px);
}

header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 2000;
    padding: 15px;
    max-width: 1000px;
    width: 100%;
    margin: 0 auto;
    transition: all .5s;
}

.logo {
    font-size: 1.8rem;
    font-weight: 700;
    line-height: 1.8rem;
    cursor: default;
    margin-bottom: 5px;
    transition: all .3s;
    color: var(--primary);
    position: relative;
    z-index: 1000;
    user-select: none;
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -o-user-select: none;
    letter-spacing: -1px;
}

.dot {
    display: inline-block;
}


/* toggler */

.toggler {
    position: relative;
    z-index: 2000;
    transition: all .3s;
}

.toggler button {
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
}

.toggler button span {
    display: block;
    width: 30px;
    height: 2px;
    background: var(--primary);
    border-radius: 5px;
}

.toggler button span:first-child,
.toggler button span:first-child+span {
    margin-bottom: 6.9px;
}


/* Menu */

.menu {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    z-index: 1000;
}

.menu-curtain {
    background: var(--primary);
    width: 0;
    height: 100%;
    position: absolute;
    top: 0;
    z-index: 1001;
}

.menu-content {
    background: var(--light);
    width: 0;
    height: 100%;
    position: absolute;
    top: 0;
    z-index: 1002;
}


/* Links */

.links {
    position: relative;
    height: 100%;
    margin: 0 auto;
    overflow: hidden;
    text-align: center;
}

.link {
    display: block;
    cursor: pointer;
    width: 300px;
    height: 5rem;
    padding: .5rem;
    overflow: hidden;
    margin: 2rem 0;
    transform: translateX(-1000px);
}

@media (max-width:768px) {
    .links {
        width: 100%;
    }
    .link {
        width: 250px;
        margin: 1rem auto;
    }
}

.link-title {
    font-weight: 700;
    font-size: 4rem;
    line-height: 4rem;
    transition: all .5s;
    letter-spacing: -1.5px;
}

@media (max-width:768px) {
    .link-title {
        font-size: 3rem;
    }
}

.link-info {
    margin-top: 1.5rem;
    font-size: 1.5rem;
    transition: all .5s;
}

@media (min-width:768px) {
    .link:hover .link-title {
        transform: translateX(-300px);
    }
    .link:hover .link-info {
        transform: translateY(-70px);
    }
}


/* Contact Links */

.contact-links {
    position: absolute;
    bottom: 1rem;
    z-index: 2000;
    right: 50%;
    transform: translateX(50%);
    gap: 15px;
}

.contact-link {
    opacity: 0;
}

.contact-links svg {
    fill: var(--primary);
    transition: all .3s;
}

.contact-links svg:hover {
    fill: var(--secondary);
}