.about {
    width: 100%;
    height: 100vh;
}

.profile-image-container,
.profile-image {
    opacity: 0;
}

.profile-image-container {
    padding: 20px;
    background: var(--primary);
    border-radius: 50%;
    width: 10rem;
    height: 10rem;
    margin-bottom: 40px;
}

.profile-image {
    width: 6rem;
}


/* Text-animation */

.intro-heading {
    height: 45px;
    text-align: center;
}

@media (max-width:768px) {
    .intro-heading h1 {
        font-size: 30px;
    }
}

.intro-text {
    height: 60px;
    text-align: center;
}

.heading,
.text {
    opacity: 0;
}