/* Shared */

.orientation,
.loader {
    width: 100%;
    height: 100vh;
}


/* Orientation */

.orientation svg {
    fill: var(--primary);
    margin-bottom: 20px;
}

.bi-phone {
    animation: rotation 3s infinite ease-in-out;
}

@keyframes rotation {
    0% {
        transform: rotate(0deg);
    }
    20% {
        transform: rotate(90deg);
    }
    40% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(0deg);
    }
}


/* Loader */

.loader {
    color: var(--white);
    position: relative;
}

.loader-content {
    position: absolute;
    top: 0;
    left: 0;
    background: var(--primary);
}

.loader-content h1 {
    transform: translateY(100px);
    letter-spacing: -.5px;
    margin: 0;
}