.projects {
    display: none;
    width: 100%;
    height: 100vh;
}

.project-curtain {
    background: #000;
    width: 0;
    height: 100%;
    position: absolute;
    top: 0;
    z-index: 1001;
}

.project {
    border-radius: 5px;
    overflow: hidden;
    height: 75vh;
    margin-top: 50px;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    position: relative;
    animation: background-effect 30s infinite;
}

@media(max-width:768px) {
    .project {
        margin-top: 0;
        height: 65vh;
    }
}

.project-content {
    bottom: 0;
    color: var(--white);
    position: absolute;
    top: 88%;
    padding: 15px;
    background: rgba(0, 0, 0, .9);
    transition: all .5s;
    z-index: 1002;
}

@media(max-width:768px) {
    .project-content {
        top: 85%;
    }
}

.project:hover .project-content {
    top: 0;
}


/* Project Tags */

.tag {
    font-size: 11px;
    background: var(--red);
    border-radius: 3px;
    padding: 3px 5px;
    color: var(--white);
}

.tag:not(:last-child) {
    margin-right: 3px;
}


/* project links */

.project-links {
    gap: 20px;
}

.project-link svg {
    transition: all .5s;
    fill: #fff;
}

.project-link:hover svg {
    fill: var(--secondary);
}


/* Project Controlls */

.project-controls {
    margin-top: 20px;
    gap: 50px;
}

.project-controls svg {
    cursor: pointer;
    transition: all .5s;
}

.project-controls svg:hover {
    transform: scale(1.2);
}

@keyframes background-effect {
    0% {
        background-position: 0% 0%;
    }
    25% {
        background-position: 20% 5%;
    }
    50% {
        background-position: 0 5%;
    }
    75% {
        background-position: 5% 20%;
    }
    100% {
        background-position: 0% 0%;
    }
}