.contact {
    display: none;
    width: 100%;
    height: 100vh;
    padding: 15px;
}

#contact-form {
    max-width: 400px;
    width: 100%;
}

input,
textarea {
    width: 100%;
    padding: 8px;
    margin-bottom: 5px;
    border: none;
    background: var(--primary);
    border: 1px solid var(--primary);
    color: var(--white);
    font-family: inherit;
    font-size: inherit;
    -webkit-appearance: none;
    transition: all .3s;
    border-radius: 5px;
}

textarea {
    resize: none;
    margin-top: 5px;
}

::placeholder {
    color: var(--white);
    opacity: 1;
}

 :-ms-input-placeholder {
    color: var(--white);
}

 ::-ms-input-placeholder {
    color: var(--white);
}

.contact-heading {
    margin-bottom: 20px;
}

.btn {
    background: var(--primary);
    color: var(--white) !important;
    line-height: 14px;
    width: 100px;
    height: 40px;
    border-radius: 5px;
}

.btn:focus,
.btn:active {
    outline: none !important;
    box-shadow: none;
}


/* sending animation */

.paper-plane {
    width: 30px;
}

.btn-text,
.success,
.failure,
.paper-plane {
    position: absolute;
}

.paper-plane {
    transform: translateX(-150px);
}

.success,
.failure {
    opacity: 0;
    transform: scale(0);
}