:root {
    --primary: #2C2F42;
    --secondary: #5B608F;
    --white: #ffffff;
    --light: #ebebeb;
    --green: #A0D468;
    --red: #D94553;
    --blue: #3BAFD9;
    --violet: #D870AD;
}

*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    outline: none !important;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

body {
    font-family: 'Poppins', sans-serif;
    color: var(--primary);
    background: var(--light);
    overflow: hidden;
}

a,
a:link,
a:visited {
    text-decoration: none;
    cursor: pointer;
    color: var(--primary);
    transition: transform .5s;
}

a:hover {
    color: var(--primary);
}

h1 {
    font-size: 38px;
    line-height: 40px;
    font-weight: 700;
    letter-spacing: -2px;
    margin: 0 3px;
}

p {
    letter-spacing: -.5px;
}

img {
    max-width: 100%;
    height: auto;
}

button {
    cursor: pointer;
    font: inherit;
    border: none;
}

ul {
    list-style: none;
}


/* Layout */

.container {
    max-width: 1000px;
    width: 100%;
    margin: 0 auto;
}

.flex {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
}

.spacer {
    flex-grow: 1;
}

.center {
    align-items: center;
    justify-content: center;
}

.h-center {
    justify-content: center;
}

.v-center {
    align-items: center;
}

.flex-column {
    flex-direction: column;
}

.full {
    width: 100%;
    height: 100%;
}

.overflow-hidden {
    overflow: hidden;
}

.position-relative {
    position: relative;
}

.box {
    position: relative;
    overflow: hidden;
}

.w-100 {
    width: 100%;
}

.h-100 {
    height: 100%;
}

.p15 {
    padding: 15px;
}

body::-webkit-scrollbar {
    width: 7px;
}

body::-webkit-scrollbar-track {
    background: var(--secondary);
}

body::-webkit-scrollbar-thumb {
    background: var(--black);
}

body::-webkit-scrollbar-thumb:hover {
    background: var(--black);
}